___CSS_LOADER_EXPORT___.push([module.id, `/* Styles for the Name Tracker extension */

.name-tracker-settings {
    padding: 5px;
}

.name-tracker_block {
    margin-bottom: 10px;
}

.name-tracker_block h4 {
    margin-top: 10px;
    margin-bottom: 10px;
    font-weight: bold;
}

/* Status Display */
.name-tracker-status-block {
    background-color: var(--SmartThemeBlurTintColor);
    border: 1px solid var(--SmartThemeBorderColor);
    border-radius: 5px;
    padding: 10px;
}

.name-tracker-status {
    font-weight: bold;
    color: var(--SmartThemeBodyColor);
    padding: 5px;
    text-align: center;
}

/* Character List */
.name-tracker-character-list {
    max-height: 400px;
    overflow-y: auto;
    border: 1px solid var(--SmartThemeBorderColor);
    border-radius: 5px;
    padding: 5px;
    margin-top: 5px;
}

.name-tracker-character {
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid var(--SmartThemeBorderColor);
    border-radius: 5px;
    background-color: var(--SmartThemeBlurTintColor);
}

.name-tracker-character:last-child {
    margin-bottom: 0;
}

/* Active/loaded character styling (characters that are loaded in SillyTavern) */
.name-tracker-character.main-character {
    border-left: 3px solid #4CAF50;
    background-color: rgba(76, 175, 80, 0.05);
}

.character-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 5px;
}

.character-name {
    font-weight: bold;
    font-size: 1.1em;
    color: var(--SmartThemeBodyColor);
}

.character-name .fa-user {
    color: #4CAF50;
    margin-right: 5px;
}

.character-name.ignored {
    color: var(--SmartThemeQuoteColor);
    text-decoration: line-through;
}

.character-badge {
    display: inline-block;
    padding: 2px 8px;
    border-radius: 3px;
    font-size: 0.75em;
    font-weight: bold;
    margin-left: 5px;
}

.character-badge.main-char {
    background-color: #4CAF50;
    color: #fff;
}

.character-badge.ignored {
    background-color: #666;
    color: #fff;
}

.character-badge.unresolved {
    background-color: #ff9800;
    color: #000;
}

/* Lorebook entry editor modal */
.lorebook-entry-editor h3 {
    margin-top: 0;
    margin-bottom: 20px;
    color: var(--SmartThemeBodyColor);
}

.editor-section {
    margin-bottom: 15px;
}

.editor-section label {
    display: block;
    font-weight: bold;
    margin-bottom: 5px;
    color: var(--SmartThemeBodyColor);
}

.editor-section small {
    display: block;
    margin-top: 3px;
    color: var(--SmartThemeQuoteColor);
    font-size: 0.85em;
}

.editor-section input,
.editor-section textarea {
    width: 100%;
    box-sizing: border-box;
}

.character-aliases {
    font-size: 0.9em;
    color: var(--SmartThemeQuoteColor);
    margin-bottom: 8px;
    font-style: italic;
}

.lorebook-entry-id {
    display: inline-block;
    margin-left: 10px;
    padding: 2px 6px;
    background: var(--SmartThemeBlurTintColor);
    border: 1px solid var(--SmartThemeBorderColor);
    border-radius: 3px;
    font-family: monospace;
    font-size: 0.85em;
    font-style: normal;
    color: var(--SmartThemeQuoteColor);
}

.character-details {
    font-size: 0.85em;
    color: var(--SmartThemeBodyColor);
    margin: 8px 0;
    padding: 8px;
    background: var(--SmartThemeBlurTintColor);
    border-left: 2px solid var(--SmartThemeBorderColor);
    border-radius: 3px;
    line-height: 1.4;
}

.character-actions {
    display: flex;
    gap: 5px;
    flex-wrap: wrap;
    margin-top: 8px;
}

.character-actions .menu_button {
    flex: 1;
    min-width: 100px;
    padding: 5px 10px;
    font-size: 0.9em;
}

.menu_button.compact {
    padding: 5px 10px;
    font-size: 0.9em;
}

.name-tracker-empty {
    color: var(--SmartThemeQuoteColor);
    font-style: italic;
    text-align: center;
    padding: 20px;
}

/* Ollama Settings */
.ollama-settings {
    background-color: var(--SmartThemeBlurTintColor);
    border-left: 3px solid var(--SmartThemeBorderColor);
    padding: 10px;
    margin-top: 5px;
    border-radius: 5px;
}

/* Merge Dialog */
.merge-dialog {
    padding: 15px;
}

.merge-dialog p {
    margin-bottom: 10px;
}

.merge-dialog strong {
    color: var(--SmartThemeBodyColor);
    font-weight: bold;
}

.merge-warning {
    color: #ff9800;
    font-size: 0.9em;
    margin-top: 10px;
    padding: 10px;
    background-color: rgba(255, 152, 0, 0.1);
    border-left: 3px solid #ff9800;
    border-radius: 3px;
}

/* Button states */
button:disabled {
    opacity: 0.5;
    cursor: not-allowed;
}

/* Flex utilities */
.flexGap5 {
    gap: 5px;
}

.flex1 {
    flex: 1;
}

/* Progress indicator */
.name-tracker-progress {
    margin: 10px 0;
    padding: 10px;
    background-color: var(--SmartThemeBlurTintColor);
    border: 1px solid var(--SmartThemeBorderColor);
    border-radius: 5px;
    text-align: center;
}

.name-tracker-progress .progress-text {
    margin-bottom: 5px;
    font-weight: bold;
}

.name-tracker-progress .progress-bar {
    width: 100%;
    height: 20px;
    background-color: var(--black50a);
    border-radius: 10px;
    overflow: hidden;
}

.name-tracker-progress .progress-fill {
    height: 100%;
    background-color: var(--SmartThemeQuoteColor);
    transition: width 0.3s ease;
}

/* Character details preview (for potential future use) */
.character-details {
    display: none;
    margin-top: 10px;
    padding: 10px;
    background-color: var(--black30a);
    border-radius: 5px;
    font-size: 0.9em;
}

.character-details.expanded {
    display: block;
}

.character-details-section {
    margin-bottom: 8px;
}

.character-details-section:last-child {
    margin-bottom: 0;
}

.character-details-label {
    font-weight: bold;
    color: var(--SmartThemeQuoteColor);
    margin-right: 5px;
}

/* Responsive adjustments */
@media (max-width: 768px) {
    .character-actions {
        flex-direction: column;
    }
    
    .character-actions .menu_button {
        width: 100%;
    }
}

/* Animation for new characters */
@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(-10px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.name-tracker-character.new {
    animation: fadeIn 0.3s ease;
}

/* System Prompt Editor */
.system-prompt-editor h3 {
    margin-top: 0;
    margin-bottom: 10px;
    color: var(--SmartThemeBodyColor);
}

.system-prompt-editor p {
    margin-bottom: 10px;
    color: var(--SmartThemeQuoteColor);
    font-size: 0.9em;
}

#system_prompt_editor {
    background-color: var(--SmartThemeBlurTintColor);
    color: var(--SmartThemeBodyColor);
    border: 1px solid var(--SmartThemeBorderColor);
    border-radius: 3px;
    padding: 10px;
    resize: vertical;
}

#system_prompt_editor:focus {
    outline: none;
    border-color: var(--SmartThemeEmColor);
}

.system-prompt-actions button {
    min-width: 100px;
}

#system_prompt_reset {
    margin-right: auto;
}


